import { create } from 'zustand';

/**
 * Player Store
 * Quản lý state của player hiện tại
 */
export const usePlayerStore = create((set) => ({
  currentPlayer: null,

  setCurrentPlayer: (player) => {
    set({ currentPlayer: player });
  },

  clearCurrentPlayer: () => {
    set({ currentPlayer: null });
  },
}));

